import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import myAvatar from '../images/avatar.jpg';

const Title=()=> {
		return (
			<div id='title'>
				<a href="https://tejaswi2737.github.io/Portfolio/">
					<div className='title-container'>
						<p className="display-5">I'm a self-taught Software Developer.</p>
						<p style={{fontSize:'18px'}}>Web development - Blockchain development - Data analysis</p>
						<img className='avatar' src={myAvatar} />
					</div>
				</a>
			</div>
		);
	
}
export default Title
